.testimonials {
  &__wrapper {
    margin-top: 70px;
    margin-bottom: 152px;
    display: grid;
    grid-template-columns: repeat(3, 240px);
    grid-gap: 8.4%;
    justify-content: center;
    @media (max-width: $ipad-screen) {
      margin-top: 30px;
      margin-bottom: 64px;
      padding: 0 20px;
      grid-template-columns: repeat(1, 90%);
      grid-row-gap: 48px;
    }
  }
}
