.heading {
  &__wrapper {
    font-weight: bold;
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1.5px;
    margin: 0 auto 20px;
    display: inline-block;
    position: relative;
    left: 50%;
    transform: translateX(-50%);

  }

  &__crown {
    position: absolute;
    top: 55px;
    left: 0;
    right: 0;
    margin: 0 auto;
    background-image: url(../images/crown.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 23px;
    height: 16px;
  }
}

@media (max-width: $small-screen) {
  .heading {
    font-size: 36px;
    &__crown {
      top: 45px;
    }
  }
}
